<template lang="html">
  <div class="vip-info">
    <img src="../../assets/img/vipFunction/n00@2x.png" alt="">
    <p class="curLevel">普通会员</p>
    <p class="nextLevel">下一级是推广师</p>
    <p class="myCoin">我的召币: 11000币</p>
    <sub-button text="VIP购买" color="#fff" :bgcolor="btnColor"></sub-button>
  </div>
</template>

<script>
import subButton from '../common/subButton'
export default {
  data () {
    return {
      test: 1
    }
  },
  computed: {
    btnColor: function () {
      return this.test === 2 ? '#2cc482' : '#c3c3c3'
    }
  },
  components: { subButton }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.vip-info {
  text-align: center;
  background: @whiteColor;
  padding: .3rem 0 .24rem 0;
  img {
    width: .9rem;
    height: .9rem;
  }
  .curLevel {
    font-size: .32rem;
    height: .72rem;
    line-height: .72rem;
    color: @curLevelColor;
  }
  .nextLevel {
    font-size: .3rem;
    height: .7rem;
    line-height: .7rem;
    color: @nextLevelColor;
  }
  .myCoin {
    font-size: .28rem;
    height: .88rem;
    line-height: .88rem;
    color: @defaultColor;
  }
}
</style>



