    <h4 class="vip-explain-title"><i class="vip-huangguan"></i>VIP特权说明</h4>
    <table class="vip-explain-table">
      <thead>
        <tr>
          <th class="vip-explain-table__left">等级</th>
          <th>普通会员</th>
          <th>推广师</th>
          <th>推广商</th>
          <th>服务部</th>
          <th>服务公司</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="vip-explain-table__left">标签数</td>
          <td>20</td>
          <td>40</td>
          <td>80</td>
          <td>120</td>
          <td>200</td>
        </tr>
        <tr>
          <td class="vip-explain-table__left">出价上限</td>
          <td>110%</td>
