<script>
export default {
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.vip-explain {
  margin-top: .3rem;
  background: @whiteColor;
  .vip-explain-title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: .3rem;
    height: .88rem;
    color: @defaultColor;
    font-weight: normal;
    .vip-huangguan {
      display: inline-block;
      width: .42rem;
      height: .31rem;
      margin-right: .2rem;
      background: url(../../assets/img/vipFunction/huangguan@2x.png);
      background-size: .42rem .31rem;
    }
  }
  .vip-explain-table {
    width: 100%;
    background: @whiteColor;
    tbody {
      tr:nth-child(even) {
        background: @vipTableColor;
      }
      .yes-table-icon {
        display: inline-block;
        width: .44rem;
        height: .44rem;
        background: url(../../assets/img/vipfunction/yes@2x.png) no-repeat;
        background-size: .44rem .44rem;
      }
    }
    th {
      background: @curLevelColor;
      color: @whiteColor;
    }
    th, td {
      width: 16%;
      height: .88rem;
      font-size: .28rem;
      text-align: center;
      border-right: 1px solid #ddd;
      border-bottom: 1px solid #ddd;
    }
    th:last-child ,td:last-child {
      border-right: none;
    }
    .vip-explain-table__left {
      width: 20%;
      font-size: .3rem;
    }
  }
}
</style>



